.container{
    flex-direction: column;
    align-items: center;
    width: 900px;
    margin-top: 10vh;
    margin-bottom: 10vh;
    border-radius: 15px;
    padding: 0 2rem;
    background-color: white;
}

h2{

    width: 470px;
    text-align: center;
    font-family: 'Lobster', cursive;
}
.qDiv{
    background-color: yellow;
}

.qnaDiv{
    margin-top: 2rem;
}
#question{
    font-size: 20px;
    font-weight: 400;
    font-family: 'Grandstander', cursive;
}

.btn{
    border-radius: 10px;
    font-size: 17px;
    margin-bottom: 1rem;
    width: 190px;
    margin-left: "1rem";
    color: white;
}
button{
    border: none;
    margin: 0 1rem;
    font-family: 'Lexend Peta', sans-serif;
}
button:hover{
    transform: scale(1.1);
}
button:focus{
    outline: none;
}
button:active{
    padding: 7px 5px;
    outline: none;
}

.submit{
    font-size: 20px;
    padding: 0.5rem 1rem;
    border-radius: 10px;
}
.options{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
}
.warning{
    font-size: 20px;
    font-family: 'Lobster', cursive;
}

h3{
    font-family: 'Lobster', cursive;

}
.resultDiv{
    height: 100vh;
    width: 100vw;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.resultItems{
    text-align: center;
    border-radius: 10px;
    width: 75vw;
    height: 65vh;
    background-color: #DFCFBE;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.result{
    font-size: 25px;
    color: tomato;
    font-family: 'Grandstander', cursive;
}

.playAgain{
    background-color: white;
    border-radius: 10px;
    color: sienna;
    font-size: 25px;
    padding: 1rem 1rem;
    background-color: lightgreen;
}

@media(max-width: 900px){
    .container{
        width: 75vw;
    }
    .options{
        flex-wrap: wrap;
        justify-content: center;
    }
    .btn{
        width: 70%;
    }
}
